function sliderTeam() {
    var swiper = new Swiper('.team .swiper', {
        slidesPerView: 5,
        spaceBetween: 50,
        navigation: {
            nextEl: '.team .swiper-button-next',
            prevEl: '.team .swiper-button-prev',
        },
        pagination: {
            el: ".team .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            1200: {
                slidesPerView: 5,
            },
            768: {
              slidesPerView: 4,
                spaceBetween: 30,
            },
            576: {
                slidesPerView: 3,
            },
            375: {
                slidesPerView: 2,
            },
            320: {
                slidesPerView: 1,
            }
        }
    })
}


$(document).ready(function() {

    sliderTeam();

    // services
    $(".header__item").hover(onIn, onOut);
    $(".header__item .header__item-show").click(function() {
        if ($(this).parents(".header__item").hasClass("header__item--active")) {
            $(this).parents(".header__item").removeClass("header__item--active")
        } else {
            $(this).parents(".header__item").addClass("header__item--active")
        }
    })

    // menu
    $('.header__burger').click(function () {
        $(this).toggleClass('header__burger--active')
        $('.header-menu').toggleClass('header-menu--active')
        $('body').toggleClass('fixed-body')
    })

    $('.header-menu__close').click(function () {
        $('.header-menu').removeClass('header-menu--active')
        $('.header__burger').removeClass('header__burger--active')
        $('body').removeClass('fixed-body')
    })

    $('.header-menu__item-title').click(function () {
        $(this).siblings('.header-menu__item-list').addClass('header-menu__item-list--active')
    })

    $('.header-menu__item-close').click(function () {
        $('.header-menu__item-list').removeClass('header-menu__item-list--active')
    })

    // Select
    $(".custom_select select").niceSelect()

    // Animation
    const animItems = document.querySelectorAll('._anim-items')

    if (animItems.length > 0) {
        window.addEventListener('scroll', animOnScroll)
        function animOnScroll(params) {
            for (let index = 0; index < animItems.length; index++) {
                const animItem = animItems[index];
                const animItemHeight = animItem.offsetHeight;
                const animItemOffset = offset(animItem).top;
                const animStart = 4;

                let animItemPoint = window.innerHeight - animItemHeight / animStart;

                if (animItemHeight > window.innerHeight) {
                    animItemPoint = window.innerHeight - window.innerHeight / animStart;
                }

                if ((pageYOffset > animItemOffset - animItemPoint) && pageYOffset < (animItemOffset + animItemHeight)) {
                    animItem.classList.add('active');
                } else {
                    if (!animItem.classList.contains('anim-no-hide')) {
                        animItem.classList.remove('active')
                    }
                }
            }
        }
        function offset(el) {
            const rect = el.getBoundingClientRect(),
                scrollLeft = window.pageXOffset || document.documentElement.scrollLeft,
                scrollTop = window.pageYOffset || document.documentElement.scrollTop;
            return { top: rect.top + scrollTop, left: rect.left + scrollLeft }
        }

        setTimeout(() => {
            animOnScroll();
        }, 300)
    }

})

function onIn() {
    if (window.innerWidth > 992) {
        let el = $(this)
        setTimeout(function() {
            if (el.is(':hover')) {
                console.log(el)
                el.addClass("header__item--active")

            }
        }, 200);
    }
}

function onOut() {
    if (window.innerWidth > 992) {
        $(this).removeClass("header__item--active")
    }
}

// video
window.addEventListener('DOMContentLoaded', function () {

    var video = document.querySelector('.video');

    video.addEventListener('click', function () {
        if (video.classList.contains('video--active')) {
            return;
        }
        video.classList.add('video--active');
        var src = video.dataset.src;

        video.insertAdjacentHTML('afterbegin', '<iframe src="'+ src +'" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></iframe>')
    })

})

